import { Grid } from '@material-ui/core';
import Box from '@material-ui/core/Box';
import PropTypes from 'prop-types';
import React from 'react';
import StateManager from '../../context/StateManager';
import Notification from '../common/Notification';
import MonitorTasksTrackerTable from '../Table/MonitorTasksTrackerTable';
import ToolbarSearch from '../Table/ToolbarSearch';

const MonitorTask = ({ onRowClick, userSelected, serviceUrl }) => {
  const { error, setError } = StateManager();
  return (
    <>
      <Box p={2} border={0}>
        <Grid container direction="row" justify="space-between">
          <Box border={0} width="70%" />
          <Box border={0} width="29%">
            <ToolbarSearch />
          </Box>
        </Grid>
      </Box>
      <Box className="owner-task-table">
        <MonitorTasksTrackerTable
          onRowClick={onRowClick}
          serviceUrl={serviceUrl}
          userSelected={userSelected}
          // selectedFilter={filterSelected}
        />
        {error ? (
          <Notification
            status="error"
            message={error}
            onClose={() => setError(null)}
            anchorOrigin={{ vertical: 'top', horizontal: 'center' }}
          />
        ) : null}
      </Box>
    </>
  );
};

MonitorTask.propTypes = {
  onRowClick: PropTypes.func.isRequired,
  userSelected: PropTypes.string,
  serviceUrl: PropTypes.string.isRequired,
};

MonitorTask.defaultProps = {
  userSelected: undefined,
};

export default MonitorTask;
