import { Grid } from '@material-ui/core';
import Box from '@material-ui/core/Box';
import PropTypes from 'prop-types';
import React, { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import StateManager from '../../context/StateManager';
import Notification from '../common/Notification';
import Filter from '../Filter/Filter';
import FilterItem from '../Filter/FilterItem';
import TasksTrackerTable from '../Table/TasksTrackerTable';
import ToolbarSearch from '../Table/ToolbarSearch';

const OwnerTask = ({ onRowClick, serviceUrl, completedTask }) => {
  const { t } = useTranslation();
  const [filterSelected, setFilterSelected] = useState('all');

  const { error, taskCount, fetchTaskCount, setError } = StateManager();

  useEffect(() => {
    fetchTaskCount(completedTask);
  }, [completedTask]);

  const count = {
    all: taskCount?.all,
    creator: taskCount?.creator,
    responsible: taskCount?.responsible,
    accountable: taskCount?.accountable,
    consulted: taskCount?.consulted,
    informed: taskCount?.informed,
  };

  return (
    <>
      <Box py={2} border={0}>
        <Grid container direction="row" justify="space-between">
          <Box border={0} width="70%">
            <Filter>
              <FilterItem
                label={t('entities.filter.all', { count: count.all })}
                onClick={() => setFilterSelected('all')}
                bgColor="#86660d"
                selected={filterSelected === 'all'}
              />
              <FilterItem
                label={t('entities.filter.creator', { count: count.creator })}
                onClick={() => setFilterSelected('creator')}
                bgColor="#61ba78"
                selected={filterSelected === 'creator'}
              />
              <FilterItem
                label={t('entities.filter.responsible', { count: count.responsible })}
                onClick={() => setFilterSelected('responsible')}
                bgColor="#b39f69"
                selected={filterSelected === 'responsible'}
              />
              <FilterItem
                label={t('entities.filter.accountable', { count: count.accountable })}
                onClick={() => setFilterSelected('accountable')}
                bgColor="#b39f69"
                selected={filterSelected === 'accountable'}
              />
              <FilterItem
                label={t('entities.filter.consulted', { count: count.consulted })}
                onClick={() => setFilterSelected('consulted')}
                bgColor="#b39f69"
                selected={filterSelected === 'consulted'}
              />
              <FilterItem
                label={t('entities.filter.informed', { count: count.informed })}
                onClick={() => setFilterSelected('informed')}
                bgColor="#b39f69"
                selected={filterSelected === 'informed'}
              />
            </Filter>
          </Box>

          <Box border={0} width="29%">
            <ToolbarSearch />
          </Box>
        </Grid>
      </Box>
      <Box className="owner-task-table">
        <TasksTrackerTable
          onRowClick={onRowClick}
          serviceUrl={serviceUrl}
          selectedFilter={filterSelected}
          completedTask={completedTask}
        />
        {error ? (
          <Notification
            status="error"
            message={error}
            onClose={() => setError(null)}
            anchorOrigin={{ vertical: 'top', horizontal: 'center' }}
          />
        ) : null}
      </Box>
    </>
  );
};

OwnerTask.propTypes = {
  onRowClick: PropTypes.func.isRequired,
  serviceUrl: PropTypes.string.isRequired,
  completedTask: PropTypes.bool,
};
OwnerTask.defaultProps = {
  completedTask: false,
};

export default OwnerTask;
