import { getDefaultOptions, request } from 'api/helpers';

const resource = 'tasks';

export const apiTaskGet = async (
  serviceUrl,
  role,
  pagination = { page: 0, pageSize: 10 },
  sorting = { sortBy: 'id', sortDirection: 'ASC' },
  search = null,
  onlyCompleted = false
) => {
  let url = `${serviceUrl}/${resource}?role=${role}&page=${pagination.page}&size=${pagination.pageSize}&sortBy=${sorting.sortBy}&sortDirection=${sorting.sortDirection}`;
  if (onlyCompleted) {
    url += `&onlyCompleted=true`;
  }
  if (search) {
    url += `&globalSearch=${search}`;
  }
  const options = {
    ...getDefaultOptions(),
    method: 'GET',
  };
  return request(url, options);
};

export const apiTaskSubordinateGet = async (
  serviceUrl,
  subordinate,
  pagination = { page: 0, pageSize: 10 },
  sorting = { sortBy: 'id', sortDirection: 'ASC' },
  search = null
) => {
  let url = `${serviceUrl}/${resource}/subordinates?page=${pagination.page}&size=${pagination.pageSize}&sortBy=${sorting.sortBy}&sortDirection=${sorting.sortDirection}`;
  if (subordinate) {
    url += `&subordinate=${subordinate}`;
  }
  if (search) {
    url += `&globalSearch=${search}`;
  }
  const options = {
    ...getDefaultOptions(),
    method: 'GET',
  };
  return request(url, options);
};

export const apiTaskCountGet = async (serviceUrl, onlyCompleted = false) => {
  let url = `${serviceUrl}/${resource}/count`;
  if (onlyCompleted) {
    url += `?onlyCompleted=true`;
  }
  const options = {
    ...getDefaultOptions(),
    method: 'GET',
  };
  return request(url, options);
};
