import React, { createContext, useEffect, useReducer } from 'react';
import reducer from 'context/rootReducer';
import { SET_USER } from 'context/type';
import PropTypes from 'prop-types';

const store = createContext([{}, () => {}]);
const { Provider } = store;

const initialState = {
  user: null,
  tasks: [],
  pagination: {
    number: 0,
    numberOfElements: 0,
    size: 5,
    totalElements: 0,
    totalePages: 0,
    pageSize: 10,
  },
  sorting: { sortBy: 'id', sortDirection: 'ASC' },
  subordinates: {},
  error: null,
  count: {},
  search: undefined,
};

const StateProvider = ({ children, keycloak, serviceUrl }) => {
  const [state, dispatch] = useReducer(reducer, { ...initialState, serviceUrl });

  useEffect(() => {
    const authenticated = keycloak.initialized && keycloak.authenticated;
    if (authenticated) {
      try {
        // eslint-disable-next-line react/prop-types
        Promise.resolve(keycloak.loadUserInfo()).then(info => {
          if (info.success) {
            // eslint-disable-next-line react/prop-types
            keycloak.loadUserInfo().success(userInfo => {
              // eslint-disable-next-line camelcase
              const { preferred_username } = userInfo;
              dispatch({ type: SET_USER, payload: { user: preferred_username.toUpperCase() } });
            });
          } else {
            // eslint-disable-next-line camelcase
            const { preferred_username } = info;
            dispatch({ type: SET_USER, payload: { user: preferred_username.toUpperCase() } });
          }
        });
      } catch (e) {
        console.error(e);
      }
    }
  }, [keycloak.initialized, keycloak.authenticated]);

  return <Provider value={{ state, dispatch }}>{children}</Provider>;
};

StateProvider.propTypes = {
  children: PropTypes.oneOfType([PropTypes.arrayOf(PropTypes.node), PropTypes.node]).isRequired,
  keycloak: PropTypes.shape({
    initialized: PropTypes.bool,
    authenticated: PropTypes.bool,
  }),
  serviceUrl: PropTypes.string.isRequired,
};

StateProvider.defaultProps = {
  keycloak: {
    initialized: false,
    authenticated: false,
  },
};

export { store, StateProvider };
