import { Box, Button, FormControl, LinearProgress, Select, Typography } from '@material-ui/core';
import { itIT } from '@material-ui/core/locale';
import { ThemeProvider } from '@material-ui/core/styles';
import createMuiTheme from '@material-ui/core/styles/createMuiTheme';
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';
import ArrowForwardIosIcon from '@material-ui/icons/ArrowForwardIos';

import { Pagination } from '@material-ui/lab';
import { format, parseISO } from 'date-fns';
import MaterialTable, { MTableBodyRow } from 'material-table';

import PropTypes from 'prop-types';
import React, { useEffect, useMemo, useRef, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { apiTaskGet } from '../../api/tasks';
import StateManager from '../../context/StateManager';
import AvatarSME from '../Avatar/AvatarSME';
import { paginationStyle, progressColor, taskProgressBar, tasksTrackerTableStyle } from './style/tasksTrackerTableStyle';
import tableIcons from './tableIcons';

const theme = createMuiTheme(
  {
    palette: {
      secondary: {
        main: '#b39f69'
      }
    }
  },
  itIT
);

const getProgressClass = progressNum => {
  return Math.ceil(progressNum / 5) * 5;
};

const options = {
  draggable: false,
  showTitle: false,
  toolbar: false,
  search: false,
  selection: false,
  emptyRowsWhenPaging: false,
  rowStyle: rowData => ({
    backgroundColor: rowData.tableData.checked ? '#b39f6933' : ''
  }),
  headerStyle: {
    color: '#9498a2'
  },
  sorting: true,
  pageSizeOptions: [5, 10, 20]
};

const TasksTrackerTable = ({ onRowClick, serviceUrl, selectedFilter, completedTask }) => {
  const tableRef = useRef(null);
  const { t } = useTranslation();
  const classes = tasksTrackerTableStyle();
  const classesProgress = taskProgressBar();
  const cssColor = progressColor();
  const { user, pagination, setTasks, setPagination, search, setError, setSearch } = StateManager();
  const [loading, setLoading] = useState(false);

  const localization = {
    toolbar: {
      nRowsSelected: `{0} ${t('entities.table.rowSelected')}`
    },
    body: {
      emptyDataSourceMessage: t('entities.table.empty')
    },
    pagination: {
      labelRowsSelect: t('entities.table.pagination.rows'),
      labelRowsPerPage: t('entities.table.pagination.rowsPerPage'),
      firstAriaLabel: '',
      firstTooltip: '',
      previousAriaLabel: '',
      previousTooltip: '',
      nextAriaLabel: '',
      nextTooltip: '',
      lastAriaLabel: '',
      lastTooltip: ''
    }
  };

  const columns = useMemo(() => [
    {
      title: 'ID',
      field: 'id',
      align: 'center',
      defaultSort: 'asc',
      type: 'numeric',
      render: rowData => <span>{rowData.id}</span>
    },
    { title: 'TASK', field: 'title', filtering: true, align: 'center' },
    {
      title: 'UTENTE/RUOLO',
      field: 'responsible',
      filtering: true,
      align: 'center',
      render: rowData => {
        let role;
        if (user) {
          role = Object.keys(rowData).find(key => String(rowData[key])?.toUpperCase() === user?.toUpperCase());
        }
        return <AvatarSME image="/entando-de-app/cmsresources/sme-tasktracker-bundle/static/img/user.jpg" user={rowData.responsible || ''} role={role || ''} />;
      }
    },
    {
      title: 'INIZIO',
      field: 'startDate',
      filtering: true,
      type: 'date',
      align: 'center',
      render: row => (row.startDate ? <span>{format(parseISO(row.startDate), 'dd/MM/yyyy')}</span> : <span>-</span>)
    },
    {
      title: 'FINE',
      field: 'endDate',
      filtering: true,
      type: 'date',
      align: 'center',
      render: row => (row.endDate ? <span>{format(parseISO(row.endDate), 'dd/MM/yyyy')}</span> : <span>-</span>)
    },
    {
      title: `PRIORITA'`,
      field: 'priority',
      filtering: false,
      align: 'center',
      render: rowData =>
        rowData.priority ? (
          <div className={`${classes.priority} ${classes[rowData.priority]}`}>{t(`entities.priority.${rowData.priority}`)}</div>
        ) : (
          <span>-</span>
        )
    },
    {
      title: `% AVANZAMENTO`,
      width: '25%',
      field: 'progress',
      filtering: true,
      sorting: false,
      type: 'numeric',
      align: 'center',
      render: rowData => {
        const { progress } = rowData;
        const classProgressCalculated = getProgressClass(progress);
        return (
          <Box display="flex" alignItems="center" minWidth="25%">
            <Box minWidth={40}>
              <Typography variant="body2" color="textSecondary">
                {`${Math.round(rowData.progress < 0 ? 100 : rowData.progress)}%`}
              </Typography>
            </Box>
            <Box width="100%" mr={1}>
              <LinearProgress
                classes={classesProgress}
                className={`${progress > 0 ? cssColor[`perc${classProgressCalculated}`] : cssColor.percNegative}`}
                variant="determinate"
                valueBuffer={100}
                value={rowData.progress < 0 ? 100 : rowData.progress}
              />
            </Box>
          </Box>
        );
      }
    }
  ]);

  const handleRowClick = (evt, row) => onRowClick(row);

  const loadData = async query => {
    try {
      setLoading(true);
      const json = await apiTaskGet(
        serviceUrl,
        selectedFilter,
        { page: pagination.number, pageSize: query.pageSize === 0 ? 5 : query.pageSize },
        {
          sortBy: query.orderBy ? query.orderBy.field : 'id',
          sortDirection: query.orderDirection
        },
        search,
        completedTask
      );
      const { payload, number, numberOfElements, totalElements, totalPages, size } = json;
      const data = payload.map(element => ({
        ...element,
        progress: element.completionPercentage
      }));
      setTasks(data, {
        number,
        numberOfElements,
        size,
        totalElements,
        totalPages
      });
      return {
        data,
        page: number,
        totalCount: totalElements
      };
    } catch (e) {
      setError(t('error.dataLoading'));
      setTasks([], {
        number: 0,
        numberOfElements: 0,
        size: 5,
        totalElements: 0,
        totalePages: 0
      });
      return {
        data: [],
        page: 0,
        totalCount: 0
      };
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    setPagination({
      number: 0,
      numberOfElements: 0,
      size: 5,
      totalElements: 0,
      totalePages: 0,
      pageSize: 10
    });
    // eslint-disable-next-line no-unused-expressions
    tableRef.current && tableRef.current.onQueryChange();
  }, [selectedFilter]);

  useEffect(() => {
    // eslint-disable-next-line no-unused-expressions
    tableRef.current && tableRef.current.onQueryChange();
  }, [search]);

  useEffect(() => {
    return () => {
      setTasks([], {
        number: 0,
        numberOfElements: 0,
        size: 5,
        totalElements: 0,
        totalePages: 0,
        pageSize: 10
      });
      setSearch('');
    };
  }, []);

  return (
    <ThemeProvider theme={theme}>
      <Box className="div__task-tracker-table-container">
        <MaterialTable
          tableRef={tableRef}
          style={{ position: 'static' }}
          isLoading={loading}
          icons={tableIcons}
          columns={columns}
          data={loadData}
          options={{ ...options, pageSize: pagination?.size ?? 5 }}
          title=""
          localization={localization}
          onRowClick={handleRowClick}
          components={{
            // Toolbar: props => <ToolbarSearch {...props} ref={tableRef} />,
            Pagination: props => (
              <CustomPagination
                serviceUrl={serviceUrl}
                // eslint-disable-next-line react/jsx-props-no-spreading
                {...props}
                count={pagination?.totalElements ?? 0}
                page={pagination?.number ?? 0}
                ref={tableRef}
              />
            ),
            Row: props => (
              // eslint-disable-next-line react/jsx-props-no-spreading,react/prop-types,react/destructuring-assignment
              <MTableBodyRow {...props} data-testid={props.data.id} className="task-list-row" />
            )
          }}
        />
      </Box>
    </ThemeProvider>
  );
};

// eslint-disable-next-line no-unused-vars
const CustomPagination = React.forwardRef((props, ref) => {
  const {
    // eslint-disable-next-line react/prop-types,react/destructuring-assignment
    count,
    // eslint-disable-next-line react/prop-types,react/destructuring-assignment
    rowsPerPage,
    // eslint-disable-next-line react/prop-types,react/destructuring-assignment
    rowsPerPageOptions,
    // eslint-disable-next-line react/prop-types,react/destructuring-assignment
    onChangeRowsPerPage,
    // eslint-disable-next-line react/prop-types,react/destructuring-assignment
    onChangePage,
    // eslint-disable-next-line react/prop-types,react/destructuring-assignment
    page
    // eslint-disable-next-line react/prop-types,react/destructuring-assignment
  } = props;
  const { pagination, setPagination } = StateManager();

  // eslint-disable-next-line react/prop-types
  const pageNumber = Math.ceil(count / rowsPerPage);

  const handleChangeRowsPerPage = event => {
    const evt = { target: { value: parseInt(event.target.value, 10) } };
    setPagination({ ...pagination, size: parseInt(event.target.value, 10) });
    onChangeRowsPerPage(evt);
  };

  const handleChangePage = (event, newPage) => {
    setPagination({ ...pagination, number: newPage - 1 });
    onChangePage(event, newPage - 1);
  };

  const hanldeNextAndPreviousPage = (event, newPage) => {
    setPagination({ ...pagination, number: newPage });
    onChangePage(event, newPage < 0 ? 0 : newPage);
  };

  // eslint-disable-next-line react/prop-types
  const rowsPerPageOptionsMemo = rowsPerPageOptions.map(m => (
    <option key={m} value={m}>
      {m}
    </option>
  ));

  const { t } = useTranslation();
  const css = paginationStyle();
  return (
    <td className={css.td}>
      <Box
        textAlign="left"
        display="flex"
        flexDirection="row"
        flexWrap="nowrap"
        justifyContent="flex-start"
        alignItems="center"
        alignContent="flex-start"
        position="absolute"
        top={20}
        right="5%"
      >
        <Box marginRight="20px">
          <Typography variant="body1" color="textSecondary">
            {t('entities.table.pagination.rowsPerPage')}
          </Typography>
        </Box>
        <Box className={css.rowsPerPage}>
          <FormControl variant="outlined" className={css.formControl} fullWidth>
            <Select native value={pagination.size} onChange={handleChangeRowsPerPage}>
              {rowsPerPageOptionsMemo}
            </Select>
          </FormControl>
        </Box>
      </Box>
      <Box
        textAlign="left"
        p={2}
        display="flex"
        flexDirection="row"
        flexWrap="nowrap"
        justifyContent="space-between"
        alignItems="center"
        alignContent="flex-start"
      >
        <Button
          onClick={evt => hanldeNextAndPreviousPage(evt, page - 1)}
          disabled={page <= 0}
          size="large"
          variant="outlined"
          className={css.button}
          startIcon={<ArrowBackIosIcon />}
        >
          <Typography variant="body2" color="textSecondary">
            {t('entities.table.pagination.previous')}
          </Typography>
        </Button>
        <Pagination
          className={css.pagination}
          size="large"
          count={pageNumber}
          page={page + 1}
          variant="outlined"
          shape="rounded"
          hideNextButton
          hidePrevButton
          onChange={handleChangePage}
        />
        <Button
          onClick={evt => hanldeNextAndPreviousPage(evt, page + 1)}
          disabled={pagination.number + 1 >= pagination.totalPages}
          size="large"
          variant="outlined"
          className={css.button}
          endIcon={<ArrowForwardIosIcon />}
        >
          <Typography variant="body2" color="textSecondary">
            {t('entities.table.pagination.next')}
          </Typography>
        </Button>
      </Box>
    </td>
  );
});

TasksTrackerTable.propTypes = {
  onRowClick: PropTypes.func.isRequired,
  serviceUrl: PropTypes.string.isRequired,
  selectedFilter: PropTypes.string.isRequired,
  completedTask: PropTypes.bool.isRequired
};

export default TasksTrackerTable;
