import { Typography } from '@material-ui/core';
import PropTypes from 'prop-types';
import React, { useEffect, useState } from 'react';
import filterStyle from './style/filterItemStyle';

const FilterItem = ({ label, onClick, bgColor, disabled, selected }) => {
  const [clicked, setClicked] = useState(!!selected);
  const classes = filterStyle();
  const handleClick = () => {
    setClicked(true);
    onClick();
  };

  useEffect(() => {
    setClicked(selected);
  }, [selected]);

  return (
    <div
      className={classes.root}
      onClick={handleClick}
      onKeyDown={handleClick}
      role="button"
      tabIndex={0}
      style={{
        backgroundColor: `${!clicked ? 'transparent' : bgColor}`,
        color: `${!clicked ? bgColor : '#ffffff'}`,
        opacity: `${disabled ? 0.4 : 1}`,
        pointerEvents: `${disabled ? 'none' : null}`,
      }}
    >
      <Typography>{label}</Typography>
    </div>
  );
};

FilterItem.propTypes = {
  label: PropTypes.string.isRequired,
  onClick: PropTypes.func,
  disabled: PropTypes.bool,
  bgColor: PropTypes.string,
  selected: PropTypes.bool,
};

FilterItem.defaultProps = {
  onClick: () => {},
  bgColor: '#2196f3',
  disabled: false,
  selected: false,
};

export default FilterItem;
