// eslint-disable-next-line no-unused-vars
import { Box, IconButton, InputBase, Paper } from '@material-ui/core';
import SearchIcon from '@material-ui/icons/Search';
import React, { useState } from 'react';
import StateManager from '../../context/StateManager';
import { toolbarStyle } from './style/tasksTrackerTableStyle';

// eslint-disable-next-line no-unused-vars
const ToolbarSearch = React.forwardRef((props, ref) => {
  const classes = toolbarStyle();
  const [value, setValue] = useState('');
  const { setSearch } = StateManager();
  return (
    // eslint-disable-next-line react/jsx-props-no-spreading
    <Box p={1}>
      <Paper className={classes.root}>
        <InputBase
          className={classes.input}
          placeholder="Ricerca..."
          inputProps={{ 'aria-label': 'ricerca' }}
          value={value}
          onChange={evt => setValue(evt.target.value)}
          onKeyUp={event => {
            event.preventDefault();
            if (event.key === 'Enter') {
              setSearch(event.target.value);
            }
          }}
        />
        <IconButton
          className={classes.iconButton}
          aria-label="search"
          onClick={() => {
            setSearch(value);
          }}
        >
          <SearchIcon />
        </IconButton>
      </Paper>
    </Box>
  );
});
export default ToolbarSearch;
