import { useContext } from 'react';
import { useTranslation } from 'react-i18next';
import { apiTaskCountGet } from '../api/tasks';
import { store } from './store';
import {
  SET_ERROR,
  SET_TASK,
  SET_PAGINATION,
  SET_SEARCH,
  SET_SUBORDINATES,
  FETCH_TASK_COUNT,
} from './type';

const action = (type, payload) => ({ type, payload });

const StateManager = () => {
  const { state, dispatch } = useContext(store);

  const { t } = useTranslation();
  const { serviceUrl } = state;

  const setTasks = (tasks, pagination) => dispatch(action(SET_TASK, { tasks, pagination }));

  const setSearch = search => dispatch(action(SET_SEARCH, { search }));

  const setSubordinates = subordinates => dispatch(action(SET_SUBORDINATES, { subordinates }));

  const setPagination = pagination => dispatch(action(SET_PAGINATION, { pagination }));

  const setError = error => dispatch(action(SET_ERROR, { error }));

  const fetchTaskCount = async onlyCompleted => {
    try {
      const json = await apiTaskCountGet(serviceUrl, onlyCompleted);
      dispatch(action(FETCH_TASK_COUNT, { count: json.payload }));
    } catch (e) {
      console.error(e);
      setError(t('error.dataLoading'));
    }
  };

  const { user, tasks, subordinates, search, pagination, count, error } = state;

  return {
    user,
    tasks,
    taskCount: count,
    pagination,
    search,
    subordinates,
    error,
    setTasks,
    fetchTaskCount,
    setSearch,
    setSubordinates,
    setPagination,
    setError,
  };
};

export default StateManager;
