import { Box, Container } from '@material-ui/core';
import Paper from '@material-ui/core/Paper';
import Tab from '@material-ui/core/Tab';
import Tabs from '@material-ui/core/Tabs';
import PropTypes from 'prop-types';
import React, { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { AuthenticatedView } from '../../auth/KeycloakViews';
import withKeycloak from '../../auth/withKeycloak';
import { OUTPUT_EVENT_TYPES } from '../../custom-elements/widgetEventTypes';
import { publishWidgetEvent } from '../../helpers/widgetEvents';
import MonitorTask from '../Tabs/MonitorTask';
import OwnerTask from '../Tabs/OwnerTask';
import { containerStyle, tabsStyle, tabStyle } from './style/taskPageStyle';

const a11yProps = index => ({
  id: `tab-${index}`,
  'data-testid': `tab-${index}`,
  'aria-controls': `tabpanel-${index}`,
});

const TabPanel = props => {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`tabpanel-${index}`}
      data-testid={`tabpanel-${index}`}
      aria-labelledby={`tab-${index}`}
      /* eslint-disable-next-line react/jsx-props-no-spreading */
      {...other}
    >
      {value === index && <Box p={0}>{children}</Box>}
    </div>
  );
};

TabPanel.propTypes = {
  children: PropTypes.node.isRequired,
  value: PropTypes.number.isRequired,
  index: PropTypes.number.isRequired,
};

const TaskPage = ({ keycloak, serviceUrl, callback, tabActive, userSelected }) => {
  const [tab, setTab] = useState(parseInt(tabActive, 10));
  const { t } = useTranslation();
  const classesContainer = containerStyle();
  const classesTab = tabStyle();
  const classesTabs = tabsStyle();

  const changeTab = (event, value) => {
    setTab(value);
  };

  useEffect(() => {
    setTab(parseInt(tabActive ?? 0, 10));
  }, [tabActive]);

  const handleClickRow = task => {
    publishWidgetEvent(OUTPUT_EVENT_TYPES.detailTask, { task });
    if (callback) {
      callback(task);
    }
  };

  return (
    <>
      <AuthenticatedView keycloak={keycloak}>
        <Paper elevation={5}>
          <Container maxWidth="xl" disableGutters className={classesContainer.container}>
            <Tabs
              value={tab}
              onChange={changeTab}
              variant="fullWidth"
              aria-label="tabs-task-tracker"
              classes={classesTabs}
            >
              <Tab
                label={t('entities.task.tabs.0')}
                /* eslint-disable-next-line react/jsx-props-no-spreading */
                {...a11yProps(0)}
                classes={classesTab}
              />
              <Tab
                label={t('entities.task.tabs.1')}
                /* eslint-disable-next-line react/jsx-props-no-spreading */
                {...a11yProps(1)}
                classes={classesTab}
              />
              <Tab
                label={t('entities.task.tabs.2')}
                /* eslint-disable-next-line react/jsx-props-no-spreading */
                {...a11yProps(2)}
                classes={classesTab}
              />
            </Tabs>
            <TabPanel value={tab} index={0}>
              <OwnerTask onRowClick={row => handleClickRow(row)} serviceUrl={serviceUrl} />
            </TabPanel>
            <TabPanel value={tab} index={1}>
              <MonitorTask
                userSelected={userSelected}
                serviceUrl={serviceUrl}
                onRowClick={row => handleClickRow(row)}
              />
            </TabPanel>
            <TabPanel value={tab} index={2}>
              <OwnerTask
                onRowClick={row => handleClickRow(row)}
                serviceUrl={serviceUrl}
                completedTask
              />
            </TabPanel>
          </Container>
        </Paper>
      </AuthenticatedView>
    </>
  );
};
TaskPage.propTypes = {
  keycloak: PropTypes.shape({}),
  serviceUrl: PropTypes.string.isRequired,
  useMock: PropTypes.bool,
  callback: PropTypes.func,
  tabActive: PropTypes.string,
  userSelected: PropTypes.string,
};

TaskPage.defaultProps = {
  keycloak: {},
  useMock: false,
  callback: () => {},
  tabActive: 0,
  userSelected: null,
};

export default withKeycloak(TaskPage);
