import { Avatar, Badge } from '@material-ui/core';
import React from 'react';
import PropTypes from 'prop-types';
import { getInitialsFromCodiceFiscale, isCodiceFiscale } from 'helpers/utils';
import { avatarStyle, badgeStyle, smallAvatarStyle } from './style/avatarSMEStyle';

const SmallAvatar = ({ role }) => {
  const css = smallAvatarStyle();
  return <Avatar className={`${css.root} ${css[role]}`}>{role.substr(0, 1).toUpperCase()}</Avatar>;
};
SmallAvatar.propTypes = {
  role: PropTypes.string.isRequired
};

const AvatarSME = ({ user, image, role }) => {
  const div = avatarStyle();
  const badge = badgeStyle();

  let initials = '';
  if (isCodiceFiscale(user)) {
    initials = getInitialsFromCodiceFiscale(user);
  } else {
    initials = user ? user.substr(0, 2) : '';
  }

  return (
    <div className={div.root}>
      <Badge
        classes={badge}
        overlap="circle"
        anchorOrigin={{
          vertical: 'bottom',
          horizontal: 'right'
        }}
        badgeContent={<SmallAvatar role={role} />}
      >
        <Avatar alt="Avatar" className={div.large} src={image}>
          {initials.toUpperCase()}
        </Avatar>
      </Badge>
    </div>
  );
};

AvatarSME.propTypes = {
  user: PropTypes.string.isRequired,
  image: PropTypes.string.isRequired,
  role: PropTypes.oneOf(['', 'creator', 'responsible', 'accountable', 'consulted', 'informed']).isRequired
};

AvatarSME.defaultProps = {};

export default AvatarSME;
